import { createSlice } from '@reduxjs/toolkit'
import call from '../middleware/call';
const user = localStorage.getItem('user')
const slice = createSlice({
    name:"userReducer",
    initialState:{
        users: [],
        currentUser:user ? user : null,
        isLoadingUser:false
    },
    reducers:{
        registration:(state, action)=>{
            state.currentUser = action.payload;
            localStorage.setItem('x-auth-token', action.payload);
            
        },
        login:(state, action)=>{
            state.currentUser = action.payload;
        },
        setCurrentUser:(state, action)=>{
            state.currentUser = action.payload;
        },
        setIsLoadingUser:(state, action)=>{
            state.isLoadingUser = action.payload
        }
    }
})
export default slice.reducer;
export const userActions = slice.actions;

export function getCurrentUser(){
    return call({ 
        url:"/regist/"+localStorage.getItem('userId'),
        method:'get',
        headers:{
            "Accept":'application/json',
            "Content-Type":'application/json',
            'x-auth-token': localStorage.getItem('x-auth-token')
        },
        onSuccess:userActions.setCurrentUser
    })
}

export function registrationUser(){
    return call({ 
        url:"/regist",
        method:'post',
        onSuccess:userActions.setCurrentUser
    })
}
export function loginUser(data){
    return call({ 
        url:"/login",
        method:'post',
        onSuccess:userActions.login
    })
}