import { useEffect, useState } from 'react'
import Header from '../../components/header/Header'
import './home.scss'
import { connect } from 'react-redux';
import { getFoods, getFoodsSlider } from '../../store/foodReducer';
import { restroActions, getRestrosFromBackend } from '../../store/restroReducer'
import AliceCarousel from 'react-alice-carousel'
import { Link, useLocation, useNavigate, useParams } from 'react-router-dom';
function Home(props) {
  const pathname = useLocation().pathname
  const navigate = useNavigate()
  const { getFoods, foods, foodsSlider, getFoodsSlider, getRestrosFromBackend, restroActions, restros} = props
  useEffect(()=>{
    getFoods(6);
    getFoodsSlider(6);
    getRestrosFromBackend()
  }, [])
  function handleDragStart(e){
    e.preventDefault();
  }
  function toRestro(id){
    navigate('/restro/'+id)
  }
const items=[];
foodsSlider.map((food, index)=>
          items.push( 
          <div style={{overflow:'hidden', height: '200px', display:'flex', alignItems:'center'}}>
            <img className='rasm'  alt='food'  
            src={`${food.image?`https://balikpapan.herokuapp.com/`+`${food.image.filePath}`:''}`} onDragStart={handleDragStart}/>
          </div>
        )   
    ) 
const foodsCarousel = [];
  foods.map((food, index)=>
    foodsCarousel.push(
      <div className='home__taste__wrapprer' >
        <div className='home__taste__block' key={index}>
          <div className='home__taste__card'>
            <img alt='food' className='home__taste__img' 
            src={`${food.image?`http://localhost:5000/`+`${food.image.filePath}`:''}`} onDragStart={handleDragStart} />
          </div>
          <div className='home__taste__description'>
            <h5>{food.foodName}</h5>
            <p>{food.description}</p>
          </div>
        </div>
    </div>
    )        
    )
  return (
    <div> 
      <Header/>
      <h1 style={{marginTop:'60px', left:'25%', color:'white', position:'absolute', zIndex:'101'}}>Explore the best food in Uzbekistan</h1>
      <div>
          <AliceCarousel items={items} autoPlay autoWidth infinite autoPlayInterval={1000} mouseTracking 
          disableButtonsControls disableDotsControls/>
      </div>
      
      <div className='home'>
        
        <h2>Best Choise</h2>
        <h5>These our best six best collections that you can find around Balikpapan City</h5>
        <div className='home__food'>
        {
          restros.length>0?restros.map((restro, index)=>
          <div to="/restro" onClick={()=>toRestro(restro._id)}  className='home__food__card' key={index}>
            <div className='img_block'>
              {
                restro.image?
                <img alt='food' 
                src={` https://balikpapan.herokuapp.com/`+`${restro.image.files[0].filePath}`}/>:
                ''
              }
            </div>
            <div className='home__food__card-info'>
              <h5>{restro.restroName}</h5>
              <p>{restro.region} {restro.district} {restro.quarter}</p>
            </div>
          </div>        
          )
          :
          <span style={{color:'#dd6161', fontWeight:'bolder'}}>Not Found!</span>
        }
        
        </div>
        <h4>New taste</h4>
        <div className='home__taste'>
          <AliceCarousel items={foodsCarousel} autoPlay autoWidth infinite autoPlayInterval={1000} mouseTracking
          disableButtonsControls disableDotsControls />
        </div>
        <span style={{color:'#dd6161', fontWeight:'bolder'}}>Not Found!</span>
        
      </div>
    </div>
  )
}

export default connect(
  ({ userReducer, foodReducer, restroReducer  })=>({ ...userReducer, ...foodReducer, ...restroReducer}),
  { getFoods, getFoodsSlider, ...restroActions, getRestrosFromBackend }
)(Home)