import { Link, useLocation, useNavigate } from 'react-router-dom';
import './header.scss'; 
import { useRef, useState } from 'react'
import { connect } from 'react-redux';
import {foodActions, searchFoods } from '../../store/foodReducer';
import {userActions } from '../../store/userReducer';
import { restroActions, searchRestrosFromBackend } from '../../store/restroReducer';

function Header(props) {
  const {pathname} = useLocation()
  const { currentUser, isLoadingUser, searchFoods, setSearchText, searchText, searchRestrosFromBackend } = props
  const navigate = useNavigate()
  function logout(){
    localStorage.removeItem('userId'); 
    localStorage.removeItem('x-auth-token')
    window.location.reload()
  }
  
  function handleChange(e){
      setSearchText(e);
      if(e===''){
        navigate('/')
      }
      if(e){
        navigate('/search')
      }
      searchRestrosFromBackend(e)
  }


  function search(){
    searchRestrosFromBackend(searchText)
  }

  return (
    <div className={`${pathname==='/'?'header-main':'header'}`}>
        <Link to={'/'}><h4 className='header__logo'>Balikpapan</h4></Link>
        <div className={`${pathname==='/'?'header-main__search':'header__search'}`}>
            <input ref={props.searchRef} value={searchText} onChange={(e)=>handleChange(e.target.value)} 
            className={`${pathname==='/'?'header-main__search-input':'header__search-input'}`}/>
            {
              pathname==='/'?'':<button onClick={()=>search()} className="btn ms-2">search</button>
            } 
        </div>
        
        <div className="header__user">
          
            {currentUser?isLoadingUser?"Loading...":
            <div className='header__user__login'>
            <Link to={'/profile'}>Hi {currentUser.firstName+" "+currentUser.lastName}</Link>
              <Link to="/">
                  <button onClick={logout}  className={`${pathname==='/'?'':'btn-info'} btn ms-2`}>Log Out</button>
              </Link>
            </div>
            :
            <div className='header__user__logout'>
              <Link to="/login">
                  <button  className="btn me-2">Login</button>
              </Link>
              <Link to="/regist">
                  <button  className={`${pathname==='/'?'':'btn-info'} btn ms-2`}>Create account</button>
              </Link>
            </div>
            }
        </div>
    </div>
  )
}

export default connect(({foodReducer, userReducer, restroReducer})=>({...foodReducer, ...userReducer, ...restroReducer}),
{
  ...foodActions, searchFoods, ...userActions, ...restroActions, searchRestrosFromBackend
}
)(Header)