import React, { useEffect, useState } from 'react'
import {connect} from 'react-redux'
import axios from 'axios';

//reducers
import { getFilesFromBackend, fileActions,   uploadFiles, 
    downloadFile, deleteFiles, searchFiles, getFiles, createFile } from '../../store/fileReducer'
import { userActions } from '../../store/userReducer' 
import { addressActions, getRegionsFromBackend, getDistrictsFromBackend, getQuartersFromBackend } from '../../store/addressReduser'
import { createRestoBackend, getRestrosFromBackend, restroActions, uploadRestroFiles} from '../../store/restroReducer';

function RestroForm(props) {
    useEffect(()=>{
        getRegionsFromBackend();
   },[]);
    
   const [formRest, setformRest] = useState({address:'', restroName:''})
    const [multiplefiles, setMultiplefiles] = useState('')
    const [singleProgress, setSingleProgress] = useState(0)

    const { setIsloadingFiles, isLoadingFiles, 
        regions, districts, quarters, getRegionsFromBackend, getDistrictsFromBackend, getQuartersFromBackend, getRestrosFromBackend,
        currentRegion, currentDistrict, currentQuarter, setRegion, setDistrict, setQuarter, createRestoBackend, restros} = props

    function resetForm(){
        setformRest({address:'', restroName:''})
        setRegion('');
        setDistrict('');
        setQuarter('');
        setMultiplefiles('');
    }
    
    function handleChangeRegion(e){
        e = JSON.parse(e)
        setRegion(e.name)
        getDistrictsFromBackend(e.id)
    }
 
    function handleChangeDistrict(e){
        e = JSON.parse(e.target.value)
        setDistrict(e.name)
        getQuartersFromBackend(currentRegion.id, e.id)
    }

    function handleChangeQuarter(e){
        e = JSON.parse(e.target.value)
        setQuarter(e.name);
    }

    function handleChangeRestro(e){
        setformRest({...formRest, [e.target.name]:e.target.value})
    }

    function handleChangeFiles(e){
        setMultiplefiles(e.target.files)
       
    }
    
    const multipleFileOptions = {
        onUploadProgress:(progressEvent)=>{
            const {loaded, total} = progressEvent;
            const percentage = Math.floor(((loaded/1000)*100)/(total/1000));
            // setmultipleProgress(percentage);
        } 
    }
    async function uploadMultipleFile(){
        const formData = new FormData();
        for (let i = 0; i < multiplefiles.length; i++) {
            formData.append('files', multiplefiles[i]);
        }

        if(multiplefiles){
             uploadRestroFiles(formData, multipleFileOptions)
                .then(({data})=>{
                    const image = data.multiplefiles._id
                    const dataForm = {...formRest, region:currentRegion, district:currentDistrict, quarter:currentQuarter, image }
                    createRestoBackend(dataForm)
                }).catch()
                .finally(()=>resetForm());
        } else {
            createRestoBackend({...formRest, region:currentRegion, district:currentDistrict, quarter:currentQuarter})
        }
    }

    function submitRestro(e){
        e.preventDefault()
        uploadMultipleFile()
     }
  return (
    <div className='profile__restro'>
    <form  id='form-restro' onSubmit={submitRestro}>
    <div className='profile__restro__left'>
        <h4>Restourant Name</h4>
        <input onChange={(e)=>handleChangeRestro(e)} value={formRest.restroName} name="restroName" 
         placeholder='Restourant Name' type={'text'}/>
        <h4>Address</h4>
        <input  onChange={(e)=>handleChangeRestro(e)} value={formRest.address} name="address" 
        placeholder='Strit..., №' type={'text'}/>
        <div className='profile__restro__left__discrict'>
            <div className='profile__restro__left__discrict__left'>
                <h4>Region</h4>
                <select onChange={(e)=>handleChangeRegion(e.target.value)}>
                    <option value={''}>
                        Region...
                    </option>
                    {
                       regions.map(region=>{ 
                        return <option  value={JSON.stringify(region)} key={region.id}>{region.name}</option>})
                    }
                </select>
            </div>
            <div className='profile__restro__left__discrict__right'>
                <h4>Discrict</h4>
                <select onChange={handleChangeDistrict}>
                    <option value={''}>
                        Discrict...
                    </option>
                    { 
                       districts.map(district=>
                        <option value={JSON.stringify(district)} key={district.id}>{district.name}</option>)
                    }
                </select>
            </div>
            <div className='profile__restro__left__discrict__right'>
                <h4>Quarter</h4>
                <select onChange={handleChangeQuarter}>
                    <option value={''}>
                        Mahalla...
                    </option>
                    {
                       quarters.map(quarter=>
                        <option value={JSON.stringify(quarter)} key={quarter.id}>{quarter.name}</option>)
                    }
                </select>
            </div>
        </div>
    </div>
    <div className='profile__restro__right'>
        <h4>Pinpoint Map</h4>
        <img />
        <input multiple onChange={(e)=>handleChangeFiles(e)} className='profile__restro__right-file' type={'file'}/>
        <button form='form-restro' className="btn btn-info">Save</button>
    </div>                 
    </form>
</div> 
  )
}

export default connect(
    ({ userReducer, addressReduser, restroReducer})=>({...userReducer, ...addressReduser, ...restroReducer}),
  
    { ...userActions,
    ...fileActions, getFilesFromBackend,  getFiles, createFile,  uploadFiles, downloadFile,deleteFiles, searchFiles,
    ...addressActions, getRegionsFromBackend, getDistrictsFromBackend, getQuartersFromBackend, 
    ...restroActions, createRestoBackend, getRestrosFromBackend
    }
)(RestroForm)