import { useForm } from 'react-hook-form';
import { Link, useNavigate } from 'react-router-dom';
import request from '../../request/request';

function Regist(props) {
    const navigate = useNavigate()
    const { register, reset, formState, handleSubmit } =useForm();
    const {errors} = formState
    
   function formSubmit(data){
        if(data.password===data.password2){
            delete data.password2
            request.register(data, '/regist').then(({data})=>{
                localStorage.setItem('userId', data.userId)
                localStorage.setItem('x-auth-token', data.token)     
                navigate('/profile');
            }).catch(err=>{alert("Bunday email bor: "+err) }).finally(()=>reset())
        }
        else{
            alert('Parollar mos emas')
        }
    }
  return (
    <div className="col-md-4 offset-4">
            <div className="card my-4">
                <div className="card-header">
                    Sign up
                </div>
                <div className="card-body">
                    <form id ='form-login' onSubmit={handleSubmit(formSubmit)} >
                        
                        <input {...register('firstName', {required:{value:true, message:"Ism majburiy"}, 
                        minLength:{value:3, message:"Kamida 3 ta harf"}})}
                        type="text" placeholder='Firstname' className="form-control mb-2"
                        />
                        {
                            errors.firstName?<span className="text-danger">{errors.firstName.message}</span>:''
                        }
                        <input {...register('lastName', {required:{value:true, message:"Familiya majburiy"}, 
                        minLength:{value:3, message:"Kamida 3 ta harf"},
                        maxLength:{value:64, message:'Ko\'pi bilan 64 ta harf'}})}
                        type="text" placeholder='Lastname' className="form-control mb-2"
                        />
                        {
                            errors.lastName?<span className="text-danger">{errors.lastName.message}</span>:''
                        }
                        <input {...register('birthDate', {required:{value:true, message:"Tug'ilgan sana majburiy"}, 
                        })} type="date" className="form-control mb-2"
                        />
                        {
                            errors.birthDate?<span className="text-danger">{errors.birthDate.message}</span>:''
                        }
                        <input 
                        {...register('email', {required:{value:true, message:"Email majburiy"},
                        minLength:{value:5, message:'Kamida 5 ta harf'}})} 
                        type = "email" placeholder='E-mail' className="form-control mb-2"/>
                        {
                            errors.email?<span className="text-danger">{errors.email.message}</span>:''
                        }
                        <input 
                        {...register('password', {required:{value:true, message:"Parol majburiy"},
                        minLength:{value:6, message:'Kamida 6 ta simvol'}})} 
                        type = "password" placeholder='Password' className="form-control mb-2"/>
                        {
                            errors.password?<span className="text-danger">{errors.password.message}</span>:''
                        }
                        <input 
                        {...register('password2', {required:{value:true, message:"Parol majburiy"},
                        minLength:{value:6, message:'Kamida 6 ta simvol'}})} 
                        type = "password" placeholder='Confirm password' className="form-control mb-2"/>
                        {
                            errors.password?<span className="text-danger">{errors.password.message}</span>:''
                        }
                        <input 
                        {...register('phone', {required:{value:true, message:"Telefon raqam majburiy"}})} 
                        type = "tel" placeholder='Phone' className="form-control mb-2"/>
                        {
                            errors.phone?<span className="text-danger">{errors.phone.message}</span>:''
                        }
                        <input {...register('contactPhone')} 
                        type = "tel" placeholder='Contact Phone' className="form-control mb-2"/>
                    </form>
                </div>
                <div className="card-footer">
                    <button form='form-login' className="btn form-control btn-success">sign up</button><br/>
                    <Link to="/login">Login</Link>
                </div>
            </div>
        </div>
  )
}

export default Regist;