import { createSlice } from '@reduxjs/toolkit'
import call from '../middleware/call'


const slice= createSlice({
    name:'foodReducer',
    initialState:{
        foods:[],
        food:'',
        isLoading:true,
        foodsSlider:[]
    },
    reducers:{   
        getFoods:(state, action)=>{
            state.foods = action.payload
        },
        getFood:(state, action)=>{
            state.food = action.payload
        },
        createFood:(state, action)=>{
            state.foods.push(action.payload);
        },
        setIsLoading:(state, action)=>{
            state.isLoading = action.payload
        },
        updateFood:(state, action)=>{
            state.foods=state.foods.map(food=>food._id===action.payload._id ? food=action.payload : food)
        },
        deleteFood:(state, action)=>{
            state.foods=state.foods.filter(food=>food._id!==action.payload._id)
        },
        getFoodsSlider:(state, action)=>{
            state.foodsSlider = action.payload
        },
    }
})

export default slice.reducer;
export const foodActions = slice.actions;

export function getFood(id){
    return call({ 
        url:"/foods/"+id,
        method:'get',
        onSuccess:foodActions.getFood
    })
};
export function getFoods(param){
    return call({ 
        url:"/foods?limit="+param,
        method:'get',
        onSuccess:foodActions.getFoods
    })
}
export function updateFood(id,data){
    return call({ 
        url:"/foods/"+id,
        method:'put',
        data,
        headers:{
            "Accept":'application/json',
            "Content-Type":'application/json',
            'x-auth-token': localStorage.getItem('x-auth-token')
        },
        onSuccess:foodActions.updateFood
    })
}
export function deleteFood(id){
    return call({ 
        url:"/foods/"+id,
        method:'delete',
        headers:{
            "Accept":'application/json',
            "Content-Type":'application/json',
            'x-auth-token': localStorage.getItem('x-auth-token')
        },
        onSuccess:foodActions.deleteFood
    })
}

export function createFood(data){
    return call({ 
        url:"/foods",      // "?imgId="+param,
        method:'post',
        data,
        headers:{
            "Accept":'application/json',
            "Content-Type":'application/json',
            'x-auth-token': localStorage.getItem('x-auth-token')
        },
        onSuccess:foodActions.createFood
    })
}

export function searchFoods(search){
    return call({ 
        url:"/foods/search?search="+search,
        method:'get',
        onSuccess:foodActions.getFoods
    })
}

export function getFoodsSlider(param){
    return call({ 
        url:"/foods?limit="+param,
        method:'get',
        onSuccess:foodActions.getFoodsSlider
    })
}
