import React, { useEffect, useRef } from 'react'
import { connect } from 'react-redux'
import Header from '../../components/header/Header'
import { restroActions, searchRestrosFromBackend } from '../../store/restroReducer'
import './search.scss'

function Search(props) {
    const {searchRestrosFromBackend, restros} = props
    useEffect(() => {
        refer();
    }, [])
    const searchRef = useRef()

    function refer(){
        searchRef.current.focus()
    }
  return (
    <div>
        <Header searchRef = {searchRef}  />
        <div className='search-page'>
            <h4>Uzbekistans restourants</h4>
            <p>Search result for "all"</p>
            <div className='search-block'>
                <div className='search-page__filters'>
                    
                </div>
                <div className='search-page__results'>
                {
          restros.length>0?restros.map((restro, index)=>
          
                    <div className='search-page__results__restro'>
                        {
                            restro.image?<img alt='restro' src={`http://localhost:5000/`+`${restro.image.files[0].filePath}`}/>:''
                        }
                        <div>
                            <h5>{restro.restroName}</h5>
                            <p>{restro.district}</p>
                            <span>description</span>
                            <p></p>
                            <p>stars</p>
                            <button className='btn border form-control'>View restrourant</button>
                        </div>
                    </div>
                    )
          :
          <span style={{color:'#dd6161', fontWeight:'bolder'}}>Not Found!</span>
        }
                </div>
            </div>
            

        </div>
       </div>
  )
}

export default connect(
    ({restroReducer})=>({...restroReducer}),
    {
        ...restroActions, searchRestrosFromBackend
    }
)(Search)