import axios from "axios"

export default ({dispatch})=>(next)=>(action)=>{

    if (action.type !== 'api/apiCall') { //shunday bo'lmasa keyingi actionlarga o'tkazadi
        return next(action)
    }

    const {url, method, data, onSuccess, headers, options} = action.payload
     axios({
        baseURL:'http://localhost:5000/api',
        url,
        method,
        data,
        options,
        headers
    }).then(({data})=>{
        if(onSuccess){
            dispatch(onSuccess(data));
        }
    }
    ).catch((e)=>{
        alert(e.message)})
    .finally(()=>{
        
        dispatch({type:'userReducer/setIsLoadingUser', payload:false})
    })

}
