import { createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import call from "../middleware/call";

const slice = createSlice({
    name:'restroReducer',
    initialState:{
        restros:[],
        currentRestro:'',
        searchText:''
    },
    reducers:{
        createRestro:(state, action)=>{
            // state.restros.push(action.payload)
        },
        getRestros:(state, action)=>{
            state.restros = action.payload
        },
        getRestro:(state, action)=>{
            state.currentRestro = action.payload
        },
        setSearchText:(state, action)=>{
            state.searchText = action.payload;
        },
        searchRestros:(state, action)=>{
            state.restros = action.payload
        }
    }
})

export default slice.reducer;
export const restroActions = slice.actions

export function getRestrosFromBackend(){
    return call({
        url:'/restros',
        method:'get',
        onSuccess:restroActions.getRestros
    })
}
export function getRestroFromBackend(id){
    return call({
        url:'/restros/'+id,
        method:'get',
        onSuccess:restroActions.getRestro
    })
}
export function createRestoBackend(data){
    return call({
        url:'/restros',
        method:'post',
        data,
        headers:{
            "Accept":'application/json',
            "Content-Type":'application/json',
            'x-auth-token': localStorage.getItem('x-auth-token')
        },
        onSuccess:restroActions.createRestro
    })
}

export async function uploadRestroFiles(data, options){
    return await axios({
        url:"http://localhost:5000/api/multiplefile",
        method:'post',
        data,
        options,
        headers:{
        "Accept":'application/json',
        "Content-Type":'application/json',
        'x-auth-token': localStorage.getItem('x-auth-token')
        }
    })
}

export function searchRestrosFromBackend(search){
    return call({
        url:'/restros/search?search='+search,
        method:'get',
        onSuccess:restroActions.searchRestros
    })
}