import { createSlice } from '@reduxjs/toolkit'
import axios from 'axios';

const slice= createSlice({
    name:'appReducer',
    initialState:{
        loader:false
    },
    reducers:{
        showLoader:(state, action)=>{
            state.loader =  true
        },
        hideLoader:(state, action)=>{
            state.loader =  false
        },
     
    }
})

export default slice.reducer;
export const appActions = slice.actions;

export function getFilesFromBackend(dirId, sort){
    return (dispatch)=>{
        let url = 'http://localhost:5000/api/files';
        if(dirId){
            url = 'http://localhost:5000/api/files?parent='+dirId
        }
        if(sort){
            url = 'http://localhost:5000/api/files?sort='+sort
        }
        if(dirId && sort){
            url = 'http://localhost:5000/api/files?parent='+dirId+'&sort='+sort
        }

        axios({
            url,
            method:'get',
            headers:{
              "Accept":'application/json',
              "Content-Type":'application/json',
              'x-auth-token': localStorage.getItem('x-auth-token')
          }
          }).then(({data})=>{
            //dispatch({type:"fileReducer/getFiles", payload:data}) yoki pastdagi
            dispatch()
            // dispatch(fileActions.setIsloading(false))
          }).catch(e=>console.log(e.responce.data.message))
    }
}

 