import React, { useEffect } from 'react'
import AliceCarousel from 'react-alice-carousel'
import { connect } from 'react-redux'
import { useParams } from 'react-router-dom'
import { getRestroFromBackend } from '../../store/restroReducer'
import './restro.scss'

function Restro(props) {
  const param = useParams()
  const {getRestroFromBackend, currentRestro} = props

  useEffect(() => {
    getRestroFromBackend(param.id)
  }, [])


  function handleDragStart(e){
    e.preventDefault();
  }
  const items = []
  currentRestro.image&&currentRestro.image.files.map((file, index)=>
  items.push(<div onDragStart={handleDragStart} key={index} style={{width:'100%', height:'250px', display:'flex', alignItems:'center'}}>
              <img width={'100%'}
                key={file._id}
                src={`http://localhost:5000/`+`${file.filePath}`}/>
              </div>)
          )

 return (
    <div className='resto'>
      <h4 className='border'>{currentRestro.restroName}</h4>
    {
        
    }
    <AliceCarousel items={items} autoPlay infinite autoWidth autoPlayInterval={1500} mouseTracking/>
    </div> 
  )
}

export default connect(
  ({restroReducer}) =>({...restroReducer}),
  {
    getRestroFromBackend
  }
)(Restro)