import logo from './logo.svg';
import './App.css';

import React, { useState, useEffect, createContext } from 'react'
import { BrowserRouter, Route, Routes, useLocation, useNavigate } from 'react-router-dom'
import Home from './pages/home/Home'
import Login from './pages/login/Login'
import Profile from './pages/profile/Profile'
import Regist from './pages/regist/Regist'
import Restro from './pages/restro/Restro'
import request from './request/request'
import { connect } from 'react-redux';
import {userActions, getCurrentUser,} from './store/userReducer'
import Search from './pages/search/Search';

export const Context = createContext();

function App(props) {
  // const [currentUser, setCurrentUser] = useState('')
  const [loading, setloadning] = useState(false)
  const pathname = useLocation().pathname
  const blockedPages = ['/profile'];
  const navigate = useNavigate()
  const { getCurrentUser, currentUser, setIsLoadingUser } = props
  
  useEffect(() => {
    loadCurrentUser()
    checkBlockedPages();
    
  }, [pathname])

  function loadCurrentUser(){
    if(localStorage.getItem('userId')){
      setIsLoadingUser(true)
      getCurrentUser();
    }
  }

  function checkBlockedPages(){
    if (blockedPages.filter(item=>item === pathname).length>0){
      if(!localStorage.getItem('x-auth-token') && !localStorage.getItem('userId')){
        navigate('/')
      }
    }
  }
  return (
     <Context.Provider value={{currentUser, loading}}>
       <Routes>
         <Route path='/' element = {<Home/>}/>
         <Route path="/restro/:id" element = {<Restro/>}/>
         <Route path='/regist' element = {<Regist/>}/>
         <Route path='/login' element = {<Login/>}/>
         <Route path='/profile' element = {<Profile/>}/>
         <Route path='/search' element = {<Search/> }/>
       </Routes>
    </Context.Provider>
  );
}

export default connect(
  ({userReducer})=>({...userReducer}),
  {
    ...userActions, getCurrentUser
  }
)(App);
