import { createSlice } from "@reduxjs/toolkit";
import call from "../middleware/call";

const slice = createSlice({
    name:'addressReducer',
    initialState:{
        regions:[],
        districts:[],
        quarters:[],

        currentRegion:'',
        currentDistrict:'',
        currentQuarter:''
    },
    reducers:{
        getRegions:(state, action)=>{
            state.regions = action.payload
        },
        getDistricts:(state, action)=>{
            state.districts = action.payload
        },
        getQuarters:(state, action)=>{
            state.quarters = action.payload
        },
        
        setRegion:(state, action)=>{
            state.currentRegion = action.payload
        },
        setDistrict:(state, action)=>{
            state.currentDistrict = action.payload
        },
        setQuarter:(state, action)=>{
            state.currentQuarter = action.payload
        }
    }
})

export default slice.reducer;
export const addressActions = slice.actions;

export function getRegionsFromBackend(){
    return call({
        url:'/address',
        method:'get',
        onSuccess:addressActions.getRegions
    })
}

export function getDistrictsFromBackend(id){
    return call({
        url:'/address/'+id,
        method:'get',
        onSuccess:addressActions.getDistricts
    })
}

export function getQuartersFromBackend(currentDistrict, qrt){
    return call({
        url:'/address/'+currentDistrict+'/'+qrt,
        method:'get',
        onSuccess:addressActions.getQuarters
    })
}