import axios from "axios";

const baseURL = "http://localhost:5000/api";
async function register(data, url) {
    return await axios({
        baseURL,
        url,
        data,
        method:'post'
    })
}

async function login(data, url) {
    return await axios({
        baseURL,
        url,
        data,
        method:'post',
        withCredentials:"include",
        headers:{
            "Accept":'application/json',
            "Content-Type":'application/json'
        },

    })
}

async function getUsers(url){
  return await axios({
        baseURL,
        url,
        method:'get',
        headers:{
            "Accept":'application/json',
            "Content-Type":'application/json',
            'x-auth-token': localStorage.getItem('x-auth-token')
        }
    })
}
async function getUser(url){
    return await axios({
          baseURL,
          url,
          method:'get',
          headers:{
            "Accept":'application/json',
            "Content-Type":'application/json',
            'x-auth-token': localStorage.getItem('x-auth-token')
        }
      })
  }

async function getFoods(url){
    return await axios({
        baseURL,
        url,
        method:'get'
    })
}
async function setFoods(url, data){
    return await axios({
        baseURL,
        url,
        method:'post',
        data,
        headers:{
            "Accept":'application/json',
            "Content-Type":'application/json',
            'x-auth-token': localStorage.getItem('x-auth-token')
        }
    })
}
export default {
    register, getUsers, getFoods, login, getUser, setFoods
}