import React, { useEffect, useState } from 'react'
import {connect} from 'react-redux'
import axios from 'axios';
import { useForm } from 'react-hook-form'
import { Modal, ModalBody, ModalFooter, ModalHeader } from 'reactstrap';
import {CircularProgressbar, buildStyles} from 'react-circular-progressbar';
import './profile.scss';
import Header from '../../components/header/Header';

//reducers
import { getFilesFromBackend, fileActions,   uploadFiles, 
    downloadFile, deleteFiles, searchFiles, getFiles, createFile } from '../../store/fileReducer'
import { userActions } from '../../store/userReducer'
import { getFoods, createFood, updateFood, deleteFood} from '../../store/foodReducer';

import RestroForm from '../../components/restroform/RestroForm'

function Profile(props) {
    const [isOpen, setIsOpen] = useState(false);
    const [isOpen2, setIsOpen2] = useState(false);

    const [currentFood, setCurrentFood] = useState('');
    const [disabled, setDisabled] = useState(false)

    const { formState, reset, setValue, register, handleSubmit } = useForm();
    const {errors} = formState

    const {foods, createFood, getFoods, updateFood, deleteFood,
        setIsloadingFiles, isLoadingFiles, 
        } = props

    useEffect(()=>{
         getFoods();
    },[]);

    const [singleFile, setSingleFile] = useState('')
    const [singleProgress, setSingleProgress] = useState(0)
    
    function modalToggle(){
        setIsOpen(p=>!p)
        reset();
        setCurrentFood('')
    }
    function modalToggle2(){
        setIsOpen2(p=>!p)
        reset();
        setCurrentFood('')
    }
    function handleChangeImg(e){
        setSingleFile(e.target.files[0])
        setSingleProgress(0)
     }
   
     const singleFileOptions = {
        onUploadProgress:(progressEvent)=>{
            const {loaded, total} = progressEvent;
            const percentage = Math.floor(((loaded/1000)*100)/(total/1000));
            setSingleProgress(percentage);
            console.log('asd',percentage);
        }
    }
 function creatingFile(datafoods){
        setDisabled(true)
        setIsloadingFiles(true)
        if(singleFile){ //rasm bulsa
            const formData = new FormData();
            formData.append('file', singleFile)
            axios({
                url:'http://localhost:5000/api/singlefile',
                method:'post',
                data:formData,
                options:singleFileOptions,
                 headers:{
                    "Accept":'application/json',
                    "Content-Type":'application/json',
                    'x-auth-token': localStorage.getItem('x-auth-token')
                }
            }).then(({data})=>{ 
                setIsloadingFiles(true)
                datafoods = {foodName:datafoods.foodName, description:datafoods.description, 
                price:parseInt(datafoods.price), image:data.file._id}
                createFood(datafoods)
                setDisabled(false)
            }).catch().finally(()=>{ setIsloadingFiles(false); modalToggle();setSingleFile('');setDisabled(false) })
        } else { //rasm bulmasa
                datafoods = {foodName:datafoods.foodName, description:datafoods.description, price:parseInt(datafoods.price)}
                createFood(datafoods);
                setIsloadingFiles(false); 
                modalToggle();
                setSingleFile('');
                setDisabled(false)
        }
       
    }

 

    function editFood(datafoods){
        setDisabled(true)    
        setIsloadingFiles(true)
        if(singleFile){ //rasm bulsa
            const formData = new FormData();
            formData.append('file', singleFile)

            axios({
                url:'http://localhost:5000/api/singlefile',
                method:'post',
                data:formData,
                options:singleFileOptions,
                headers:{
                    "Accept":'application/json',
                    "Content-Type":'application/json',
                    'x-auth-token': localStorage.getItem('x-auth-token')
                }
            }).then(({data})=>{ 
                
                setIsloadingFiles(true)
                datafoods = {foodName:datafoods.foodName, description:datafoods.description, 
                price:parseInt(datafoods.price), image:data.file._id}
                updateFood(currentFood._id, datafoods)
                setDisabled(false)
            }).catch().finally(()=>{ setIsloadingFiles(false); modalToggle();setSingleFile(''); setDisabled(false) })
        } else { //rasm bulmasa
                datafoods = {foodName:datafoods.foodName, description:datafoods.description, price:parseInt(datafoods.price)}
                updateFood(currentFood._id, datafoods);
                setIsloadingFiles(false); 
                modalToggle();
                setSingleFile('');
                setDisabled(false)
        }
       
    }
    function formSubmit(data){
        if(currentFood){
            editFood(data)
            
        } else {
            creatingFile(data)
        }
        // getFoods()
    }
    function startEditFood(food){
        modalToggle()
        setCurrentFood(food)
        setValue('foodName', food.foodName);
        setValue('description', food.description);
        setValue('price', food.price);
    }

    
    
  return (
    <div>
        <Header/>
        <div className='profile'>
            <h2 className='profile__title'>My Profile</h2>
            <div className='profile__user'>
                <div className='profile__user__img-content'>
                    <img className='profile__img-content__img-main'/>
                    <div className='profile__img-content__img-edit'>
                        <img className='profile__img-content__img-edit__img'/>
                        <button className='profile__img-content__img-edit__button'>E</button>
                        <h6 className='profile__img-content__img-edit__text'>Edit Photo</h6>
                    </div>
                </div>
                <div className='profile__user__info'>
                    <form className='profile__user__info__form'>
                        <div className='profile__user__info__form-left'>
                            <h4>Owner First Name</h4>
                            <input/>
                            <h4>BirthDate</h4>
                            <div className='profile__user__info__form-left__birthdate'>
                                <select>
                                    <option>January</option>
                                </select>
                                <select>
                                    <option>1</option>
                                </select>
                                <select>
                                    <option>1980</option>
                                </select>
                            </div>
                            <h4>E-mail</h4>
                            <input/>
                            <h4>Phone Number</h4>
                            <input/>
                        </div>
                        <div className='profile__user__info__form-right'>
                            <h4>Last Name</h4>
                            <input/>
                            <div>
                                <h4>Password</h4>
                                <input/>
                                <h4>Contact Person</h4>
                                <input/>
                            </div>
                        </div>
                        
                    </form>
                </div>
            </div>
            <RestroForm/>
            <div className='profile__menu'>
                <h2 className='profile__menu__title'>Menu</h2>
                <Modal isOpen={isOpen} toggle={modalToggle}>
                    <ModalHeader>
                        Add Menu
                    </ModalHeader>
                    <ModalBody>
                        <form id='form-foods' onSubmit={ handleSubmit(formSubmit)}>
                            <input {...register('foodName', {required:{value:true, message:"Majburiy maydon"},
                             minLength:{value:3, message:"3 tadan ko'p harf kirit"}})} 
                             className='form-control my-2' placeholder='Food Name'/>
                             {
                                errors.foodName?<span className='text-danger'>{errors.foodName.message}</span>:''
                             }
                            <input {...register('description', {required:{value:true, message:"Majburiy maydon"},
                             minLength:{value:3, message:"3 tadan ko'p harf kirit"}})} 
                            className='form-control my-2' placeholder='Description'/>
                             {
                                errors.description?<span className='text-danger'>{errors.description.message}</span>:''
                             }
                            <input {...register('price', {required:{value:true, message:"Majburiy maydon"},
                             min:{value:0, message:"0 dan kichik qiymat kiritdingiz"}})} 
                            className='form-control my-2' placeholder='Price' type={'number'}/>
                            {
                                errors.price?<span className='text-danger'>{errors.price.message}</span>:''
                             }
                            <input onChange={(e)=>handleChangeImg(e)} className='form-control my-2' type={'file'}/> 
                            
                        </form>
                    </ModalBody>
                    <ModalFooter>
                        <button disabled={disabled} form='form-foods' className='btn btn-success'>Save</button>
                        <button onClick={modalToggle} className='btn btn-danger'>Cancel</button>
                    </ModalFooter>
                </Modal>

                <Modal isOpen={isOpen2} toggle={modalToggle2}>
                    <ModalHeader>
                        View
                    </ModalHeader>
                    <ModalBody>
                        <form id='form-view' onSubmit={handleSubmit()}>
                            <input {...register('foodName', {required:{value:true, message:"Majburiy maydon"},
                             minLength:{value:3, message:"3 tadan ko'p harf kirit"}})}
                             className='form-control my-2' placeholder='Food Name'/>
                             {
                                errors.foodName?<span className='text-danger'>{errors.foodName.message}</span>:''
                             }
                            <input {...register('description', {required:{value:true, message:"Majburiy maydon"},
                             minLength:{value:3, message:"3 tadan ko'p harf kirit"}})} 
                            className='form-control my-2' placeholder='Description'/>
                             {
                                errors.description?<span className='text-danger'>{errors.description.message}</span>:''
                             }
                            <input {...register('price', {required:{value:true, message:"Majburiy maydon"},
                             min:{value:0, message:"0 dan kichik qiymat kiritdingiz"}})} 
                            className='form-control my-2' placeholder='Price' type={'number'}/>
                            {
                                errors.price?<span className='text-danger'>{errors.price.message}</span>:''
                             }
                            <input onChange={(e)=>handleChangeImg(e)} className='form-control my-2' type={'file'}/> 
                            
                        </form>
                    </ModalBody>
                    <ModalFooter>
                        <button disabled={disabled} form='form-view' className='btn btn-success'>Save</button>
                        <button onClick={modalToggle2} className='btn btn-danger'>Cancel</button>
                    </ModalFooter>
                </Modal>
                {/* <CircularProgressbar className='col-1' value={singleProgress} text={`${singleProgress}%`}
                    styles={buildStyles({
                        rotation:0.25,
                        strokeLinecap:'butt',
                        textSize:'16px',
                        pathTransitionDuration:0.5,
                        pathColor: `rgba(255, 136, 136, ${singleProgress/100})`,
                        textColor:'#f88',
                        trailColor:'#d6d6d6',
                        backgroundColor:'#3e98c7'
                    })}
                    /> */}
                <table className='table'>
                    <thead>
                        <tr>
                            <td colSpan={5} rowSpan={1}>
                                <div className='float-end'>
                                    <button onClick={modalToggle} className='btn btn-danger '>Add Menu</button>
                                </div>
                            </td>
                        </tr>
                        <tr>
                            <th>№</th><th>Listed Menu</th><th>Description</th><th>Price</th><th>Action</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            isLoadingFiles?"Loading":
                            foods.map((food, index)=>
                                <tr key={index}>
                                    <td>{index+1}</td><td>{food.foodName}</td><td>{food.description}</td><td>{food.price}</td>
                                    <td>
                                        <button onClick={()=>startEditFood(food)} className='btn btn-danger me-2'>Edit</button>
                                        <button onClick={()=>modalToggle2(food)} className='btn btn-danger me-2'>view</button>
                                        <button onClick={()=>deleteFood(food._id)}  className='btn btn-danger'>delete</button>
                                    </td>
                                    
                                </tr>
                            )
                        }
                    </tbody>
                </table>
            </div>
        </div>
    </div>
  )
}

export default connect(
    ({fileReducer, userReducer, foodReducer})=>({...fileReducer, ...userReducer, ...foodReducer }),
  { ...userActions,
    ...fileActions, getFilesFromBackend,  getFiles, createFile,  uploadFiles, downloadFile,deleteFiles, searchFiles,
    createFood, getFoods, updateFood, deleteFood,
  }
)(Profile)