import { createSlice } from '@reduxjs/toolkit'
import axios from 'axios';
import call from '../middleware/call'

const slice= createSlice({
    name:'fileReducer',
    initialState:{
        files:[],
        isLoadingFiles:false,
        currentFile:''
    },
    reducers:{
         
        getFiles:(state, action)=>{
            state.files = action.payload
        },
        
        setCurrentFile:(state, action)=>{
            state.currentFile = action.payload
        },
        setIsloadingFiles:(state, action)=>{
            state.isLoading = action.payload
        },
        
        uploadFile:(state, action)=>{
            //addni ishlat
        },
        deleteFile:(state, action)=>{
            state.files = state.files.filter(file=>file._id!=action.payload)
        }
    }
})



export default slice.reducer;
export const fileActions = slice.actions;

export function getFiles(){ 
    return call({ 
        url:"/singlefiles",
        method:'get',
        onSuccess:fileActions.getFiles
    })
}

export function createFile(data, options){ 
    return call({ 
        url:"/singlefile",
        method:'post',
        data,
        options,
        headers:{
            "Accept":'application/json',
            "Content-Type":'application/json',
            'x-auth-token': localStorage.getItem('x-auth-token')
        },
        onSuccess:fileActions.setCurrentFile
    })
}
export function getFilesFromBackend(dirId, sort){
    return (dispatch)=>{
        dispatch(fileActions.setIsloading(true))
        let url = 'http://localhost:5000/api/files';
        if(dirId){
            url = 'http://localhost:5000/api/files?parent='+dirId
        }
        if(sort){
            url = 'http://localhost:5000/api/files?sort='+sort
        }
        if(dirId && sort){
            url = 'http://localhost:5000/api/files?parent='+dirId+'&sort='+sort
        }

        axios({
            url,
            method:'get',
            headers:{
              "Accept":'application/json',
              "Content-Type":'application/json',
              'x-auth-token': localStorage.getItem('x-auth-token')
          }
          }).then(({data})=>{
            //dispatch({type:"fileReducer/getFiles", payload:data}) yoki pastdagi
            dispatch(fileActions.setFiles(data))
            // dispatch(fileActions.setIsloading(false))
          }).catch(e=>console.log(e.responce.data.message))
          .finally(()=>{dispatch(fileActions.setIsloading(false))})
    }
}

 export function createDir(dirId, name){
    return (dispatch)=>{
    axios({
      url:`http://localhost:5000/api/files`,
      method:'post',
      data:{
        name,
        parent:dirId,
        type:'dir',
      },
      headers:{
        "Accept":'application/json',
        "Content-Type":'application/json',
        'x-auth-token': localStorage.getItem('x-auth-token')
    }}
    ).then((res)=>{
        dispatch(fileActions.addFile(res.data))
    }).catch(e=>alert(e.message)).finally(()=>{})
    }
}

export function uploadFiles(file, dirId){
    return (dispatch)=>{
        const formData = new FormData();
        formData.append('file', file)
        if(dirId){
            formData.append('parent', dirId)
        }
   
        axios.post(`http://localhost:5000/api/files/upload`, 
        formData,
        {
        headers:{
            "Accept":'application/json',
            "Content-Type":'application/json',
            'x-auth-token': localStorage.getItem('x-auth-token')
            },
        onUploadProgress: progressEvent=>{
            const totalLength = progressEvent.lenghtComputable?progressEvent.total:progressEvent.target.getResponseHeader('content-length')|| progressEvent.target.getResponseHeader('x-decompressed-content-length')
            console.log('total', totalLength);
            if(totalLength){
                let progress = Math.round((progressEvent.loaded*100)/totalLength)
                console.log(progress);
                }
            }
        }  
    )
    .then((res)=>{
     dispatch(fileActions.addFile(res.data))
    }).catch(e=>{ alert(e.res.data.message)}).finally(()=>{})
    }
}

export async function downloadFile(file){
    const responce = await fetch("http://localhost:5000/api/files/download?id="+file._id,
    {
        headers:{'x-auth-token': localStorage.getItem('x-auth-token')}
    })
    if (responce.status===200){
        const blob = await responce.blob();
        const downloadUrl = window.URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = downloadUrl
        link.download = file.name;
        document.body.appendChild(link);
        link.click();
        link.remove()
    }

}


export function deleteFiles(file){
    return (dispatch)=>{
   
        axios.delete(`http://localhost:5000/api/files?id=`+file._id,
        {
        headers:{
            "Accept":'application/json',
            "Content-Type":'application/json',
            'x-auth-token': localStorage.getItem('x-auth-token')
            }
        }  
    )
    .then((res)=>{
     dispatch(fileActions.deleteFile(file._id))
     alert(res.data.message)
    }).catch(e=>{ alert(e?.data?.message)}).finally(()=>{})
    }
}

export function searchFiles(search){
    return (dispatch)=>{
        dispatch(fileActions.setIsloading(true))
          axios({
            url:"http://localhost:5000/api/files/search?search="+search,
            method:'get',
            headers:{
              "Accept":'application/json',
              "Content-Type":'application/json',
              'x-auth-token': localStorage.getItem('x-auth-token')
          }
          }).then(({data})=>{
            dispatch(fileActions.setFiles(data))
          }).catch(e=>console.log(e.responce.data.message))
          .finally(()=>{dispatch(fileActions.setIsloading(false))})
    }
}