import { useForm } from 'react-hook-form'
import { Link, useNavigate } from 'react-router-dom';
import request from '../../request/request';

function Login() {
    const navigate = useNavigate()
    const { register, reset, formState, handleSubmit } =useForm();
    const {errors} = formState

    function formSubmit(data){        
      request.register(data, '/login').then((res)=>{
          if(res.status===200){
              localStorage.setItem('userId', res.data.userId)
              localStorage.setItem('x-auth-token', res.data.token)
          }
        navigate('/profile');
        reset();
      }).catch(err=>{alert("Serverda xatolik: "+err.message) })
    }
    
  return (
    <div className="col-md-4 offset-4">
            <div className="card my-4">
                <div className="card-header">
                    Sign in
                </div>
                <div className="card-body">
                    <form id ='form-login' onSubmit={handleSubmit(formSubmit)} >
                        <input 
                        {...register('email', {required:{value:true, message:"Email majburiy"},
                        minLength:{value:5, message:'Kamida 5 ta harf'}})} 
                        type = "email" placeholder='E-mail' className="form-control mb-2"/>
                        {
                            errors.email?<span className="text-danger">{errors.email.message}</span>:''
                        }
                        <input 
                        {...register('password', {required:{value:true, message:"Parol majburiy"},
                        minLength:{value:6, message:'Kamida 6 ta simvol'}})} 
                        type = "password" placeholder='Password' className="form-control mb-2"/>
                        {
                            errors.password?<span className="text-danger">{errors.password.message}</span>:''
                        }
                    </form>
                </div>
                <div className="card-footer">
                    <button form='form-login' className="btn form-control btn-success">sign in</button><br/>
                    <Link to="/regist">Register</Link>
                </div>
            </div>
        </div>
  )
}

export default Login;